body {
  font-family: "Open Sans", Arial, sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #000;
}

body.menu-open {
  overflow: hidden;
}

.scroll-disabled {
  position: fixed;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
}

.wrap {
  margin: 0 auto;
  max-width: 1330px;
  padding: 0 20px;
  width: 100%;
