/*--------------------------------------------------------------
# Layout
--------------------------------------------------------------*/
a,
button {
  appearance: none;
  outline: none;
}

@media only screen and (min-width: 992px) {
  a:focus,
  button:focus {
    outline: 1px dashed rgba(241, 89, 42, 0.2);
  }
}

button,
input[type='button'],
input[type='submit'] {
  background: none;
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 0;
}

.btn {
  background-color: #0984e3;
  box-shadow: 0 3px #000;
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  padding: 13px 20px;
  transition: ease 0.3s;
}

@media only screen and (min-width: 1025px) {
  .no-touch .btn:hover {
    background-color: #67d5f4;
  }
}

@media only screen and (max-width: 991.98px) {
  .btn {
    font-size: 18px;
  }
}

input,
textarea {
  background: #fff;
  border-radius: 0;
  box-shadow: none;
  color: #000;
  height: 25px;
  padding: 0 10px;
}

input::placeholder,
textarea::placeholder {
  color: #2d3436;
  font-style: italic;
}

.input {
  border: 1px solid #2d3436;
  height: 50px;
  margin: 15px 0;
  max-width: 280px;
  width: 100%;
}

select {
  appearance: none;
  border: 1px solid #2d3436;
  cursor: pointer;
  height: 50px;
  margin: 15px 0;
  max-width: 100%;
  padding: 0 15px;
  width: 100%;
  background-image: url("../../../assets/svg/ico-chevron-down-blue.svg");
  background-repeat: no-repeat;
  background-position: 96%;
  background-size: 20px;
}

@media only screen and (max-width: 991.98px) {
  select {
    font-size: 12px;
  }
}

.form-left .parent-input {
  max-width: 100%;
  margin-right: 26px;
}

.form-left .child-input {
  max-width: 100%;
  margin-right: 0;
}

.input__support-text {
  color: #0984e3;
  margin-left: 15px;
}

.input__support-text [class^='font-ico'] {
  font-size: 22px;
  vertical-align: sub;
  margin-right: 5px;
}

@media only screen and (max-width: 1199.98px) {
  .input__support-text {
    display: block;
    margin-left: 0;
  }
}

.input-icon__text {
  color: #0984e3;
  font-size: 12px;
}

.input__phone {
  max-width: 50%;
  margin-right: 26px;
}

.input__email {
  max-width: 100%;
}

.input--without-icon select {
  max-width: 100%;
  width: 100%;
}

.input__date {
  height: 50px;
  width: 50%;
}

.form-left__double {
  align-items: center;
  display: flex;
}

/*--------------------------------------------------------------
# Plugins
--------------------------------------------------------------*/
.slick-slider {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  display: block;
  position: relative;
  touch-action: pan-y;
  user-select: none;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  transform: translate3d(0, 0, 0);
}

.slick-list {
  display: block;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: relative;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-track {
  display: block;
  left: 0;
  position: relative;
  top: 0;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  display: none;
  float: left;
  height: 100%;
  min-height: 1px;
  outline: 0;
}

[dir='rtl'] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/*--------------------------------------------------------------
# Print Styles
--------------------------------------------------------------*/
@media print {
  body {
    background: #fff;
    color: #000;
    margin: 0;
    padding: 0;
    text-align: left;
  }
  a:link, a:visited {
    background: #fff;
    color: #000;
    text-decoration: underline;
  }
  a:not([href='javascript:;']):after {
    content: "[" attr(href) "]";
  }
  h1, h2, h3, h4, h5, h6 {
    background: none;
    color: #000;
  }
  .temp {
    display: none;
  }
}

/*--------------------------------------------------------------
# Components
--------------------------------------------------------------*/
.is-beta {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
}

.is-beta__container {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
  width: 90px;
  height: 75px;
  overflow: hidden;
}

.is-beta__label {
  width: 150px;
  margin-top: 2px;
  margin-left: -48px;
  padding: 8px;
  background-color: #c1d72e;
  font-size: 18px;
  font-weight: 900;
  color: #fff;
  text-transform: uppercase;
  transform: rotate(-40deg);
  transition: background-color 0.3s linear;
}

@media only screen and (min-width: 1025px) {
  .no-touch .is-beta__label:hover {
    background-color: #9eb022;
  }
}

.is-beta__close {
  width: 80px;
  height: 80px;
  background-color: #fff;
}

.is-beta__close span {
  position: relative;
  display: block;
  width: 26px;
  height: 26px;
  margin: 0 auto;
  background-color: #c1d72e;
  border-radius: 50%;
  transition: background-color 300ms linear;
}

.is-beta__close span::before, .is-beta__close span::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  width: 2px;
  height: 16px;
  background-color: #fff;
  border-radius: 2px;
}

.is-beta__close span::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.is-beta__close span::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

@media only screen and (min-width: 1025px) {
  .no-touch .is-beta__close:hover span {
    background-color: #9eb022;
  }
}

@media only screen and (max-width: 767.98px) {
  .is-beta__close {
    width: 40px;
    height: 40px;
  }
}

.is-beta__cta {
  margin-top: 35px;
  padding: 14px 25px;
  background-color: #0061ad;
  font-size: 18px;
  font-weight: 700;
  color: #fff;
  transition: background-color 300ms linear;
}

@media only screen and (min-width: 1025px) {
  .no-touch .is-beta__cta:hover {
